.menu-bar button {
	border: none;
	padding: 0;
	background-color: transparent;
	height: 25px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	position: relative;
	transform-origin: center center;
	transform: rotate(0deg);
	cursor: pointer;
	transition: transform 300ms ease;
}
.menu-bar button span {
	height: 3px;
	width: 35px;
	display: block;
	background: var(--primary-color);
	cursor: pointer;
	transition: all 0.3s ease-in-out 0s;
	margin-left: auto;
}
.dark .menu-bar button span {
	background: var(--white-color);
}
.menu-bar button span:nth-child(2) {
	width: 40px;
	transition-delay: 200ms;
}
.menu-bar button span:nth-child(3) {
	width: 30px;
}
.menu-bar button span:nth-child(4) {
	position: absolute;
	top: -8px;
	left: 50%;
	transform: translateX(-50%);
	display: block;
	width: 3px;
	height: 0;
	transition: height 400ms;
}
.menu-bar button.active {
	transform: rotate(45deg);
	transition-delay: 400ms;
}
.menu-bar button.active span:nth-child(1) {
	width: 0;
}
.menu-bar button.active span:nth-child(3) {
	width: 0;
}
.menu-bar button.active span:nth-child(4) {
	height: 40px;
	transition: height 200ms ease;
	transition-delay: 200ms;
}
/* mobile menu */
.mobile-menu {
	transform: scaleY(0);
	transition: all 0.5s;
}
.mobile-menu.active {
	transform: scaleY(1);
}

