/**----------------------------------------
START: Theme cursor CSS
----------------------------------------*/

body.tj-magic-cursor #magic-cursor {
	display: block;
}
#magic-cursor {
	position: absolute;
	display: none;
	width: 10px;
	height: 10px;
	pointer-events: none;
	z-index: 99999;
	opacity: 0;
}

#ball {
	position: fixed;
	display: flex;
	align-items: center;
	pointer-events: none;
	background-color: var(--primary-color) !important;
	border-radius: 50%;
	justify-content: center;
	mix-blend-mode: difference;
	color: transparent;
	border: 0;
}

/* Ball view 
============= */
#ball .ball-view {
	position: absolute;
	opacity: 0;
	visibility: hidden;
	padding: 0 5px;
	line-height: 1.1;
	text-align: center;
	transform: scale(0);
	font-family: var(--tj-ff-body);
	font-weight: 400;
	font-size: 16px;
	text-transform: capitalize;
	color: var(--white-color);
}

/* Ball drag 
============= */
#ball .ball-drag {
	position: absolute;
	display: block;
	width: 100%;
	padding: 0 5px;
	font-size: 15px;
	font-weight: 600;
	color: var(--black-color);
	line-height: 1.2;
	text-align: center;
	transition: all 0.3s;
}
#ball .ball-drag::before,
#ball .ball-drag::after {
	position: absolute;
	top: 50%;
	margin-top: -5px;
	font-size: 19px;
	color: var(--white-color);
	height: 10px;
	line-height: 10px;
	font-family: "Font Awesome 6 Pro";
	font-weight: 900;
	display: inline-block;
	font-style: normal;
	font-variant: normal;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
}

#ball .ball-drag::before {
	content: "\f104"; /* Font Awesome */
	left: 0;
	transform: translate3d(-30px, 0, 0);
	transition: all 0.25s;
}
#ball .ball-drag::after {
	content: "\f105"; /* Font Awesome */
	right: 0;
	transform: translate3d(30px, 0, 0);
	transition: all 0.25s;
}
#ball.with-blur {
	-webkit-backdrop-filter: blur(5px);
	backdrop-filter: blur(5px);
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

/* Ball close 
============== */
#ball.ball-close-enabled {
	opacity: 1 !important;
}
#ball .ball-close {
	position: absolute;
	padding: 0 5px;
	font-size: 14px;
	font-weight: 600;
	color: var(--black-color);
	line-height: 1;
	text-align: center;
}

.tj-magnetic-wrap {
	display: flex;
	justify-content: center;
	align-items: center;
}
#tabs li.active a {
	background-color: var(--primary-color-light);
}

/* Tab fade effect */
.fade {
	opacity: 0;
	visibility: hidden; /* Ensures the element is not interactive */
	transition: opacity 0.8s ease, visibility 0s 0.8s; /* Delay visibility change */
	width: 100%;
}

.fade.show {
	opacity: 1;
	visibility: visible;
	transition: opacity 0.8s ease, visibility 0s 0s; /* No delay in visibility */
}

/* Tab content */
.tab-pane {
	display: none; /* Hide by default */
}

.tab-pane.show {
	display: block; /* Show when active */
}

